* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
::-webkit-scrollbar {
	display: none;
}
body {
	background-color: black;
}
.app {
	display: flex;
	flex-direction: column;
	text-align: center;
	justify-items: center;
	width: 100%;
}

input {
	padding: 15px 20px;
	outline: none;
	border: 2px solid black;
	border-radius: 5px;
	transition: 0.3s;
}
input:focus {
	border: 2px solid blue;
}
.twitchChat {
	width: 400px;
}

@media (max-width: 720px) {
	.app {
		height: 100vh;
	}
	.videoAndChat {
		height: fit-content;
	}
	.playerAndChat {
		flex-direction: column;
		height: fit-content;
	}
	.twitchChat {
		height: 200%;
		width: 100%;
	}
}
